import { authAPI } from '../api/api';
import { stopSubmit } from 'redux-form';
import Message from '../components/Dialogs/Message/Message';
const SET_USER_DATA = 'social-network/auth/SET_USER_DATA';



let initialState = {
    userId: null,
    login: null,
    email: null,
    isAuth: false
};


const authReducer = (state=initialState, action ) => { 
    
    switch(action.type) {
        case SET_USER_DATA: 
    
            return {
                ...state,
                ...action.data
                
         };
       
        default:
          return state;
      }  
     
  };
        


export const setAuthUserData = (userId, login, email, isAuth) => (
    {type: SET_USER_DATA, data:{userId, login, email, isAuth}}

    );
export const getAuthUserData = () => async (dispatch) => {
      let response = await authAPI.me(); //await instead of then. let response =значение промиса
         
            if (response.data.resultCode === 0) {
              let { userId, login, email } = response.data.data;
              dispatch(setAuthUserData(userId, login, email, true));
      
            }
};


export const login = (email, password, rememberMe) => async (dispatch) => { //санка должна быть асинхронной
     
        let response = await authAPI.login(email, password, rememberMe);
            if (response.data.resultCode === 0) {
                dispatch(getAuthUserData());
            } 
            else {
                let message = response.data.messages.length > 0 ? response.data.messages[0] : "Some error";
                dispatch(stopSubmit("login", {_error: message}));
            }
    };

export const logout = (email, password, rememberMe, isAuth) => async (dispatch) => {
        let response = await authAPI.logout();
        
            if (response.data.resultCode === 0) {
                dispatch(setAuthUserData(null, null, null, false));
            }
};
 
export default authReducer;

