import React from "react";
import { connect } from "react-redux";
import {
    setCurrentPage,
    getUsersThankCreator,
    follow,
    unfollow
} from '../../redux/user-reducer';
import Users from './Users';
import Preloader from '../common/Preloader/Preloader';
import { compose } from 'redux';
import { withAuthRedirect } from '../../hoc/withAuthRedirect';
import {
    getPageSize,
    getUsers,
    getTotalUsersCount,
    getCurrentPage,
    getSsFetching,
    getFollowingInProgress,
} from '../../redux/user-selectors';



class UsersContainer extends React.Component {//extends React тк внутри Реакта создается объект new User,в него по умолчанию из 
    // берем Реакта методы, свойста 
    // не прописали constructor and super тк constructor только делегирует конструирование родительскому конструктору
    //то прокидывание props проходит автоматически, без аргументов и тд
    // constructor(props) {
    //     super(props);
    // }
    componentDidMount() {
        const { getUsers, currentPage, pageSize } = this.props;
        getUsers(currentPage, pageSize);

    }
    onPageChanged = (pageNumber) => {
        const { getUsers, pageSize } = this.props;
        getUsers(pageNumber, pageSize);
    }
    render() {

        return <>
            {this.props.isFetching ? <Preloader /> : null}

            <Users totalItemsCount={this.props.totalItemsCount}
                onPageChanged={this.onPageChanged}
                users={this.props.users}
                currentPage={this.props.currentPage}
                pageSize={this.props.pageSize}
                follow={this.props.follow}
                unfollow={this.props.unfollow}
                toggleFollowingProgress={this.props.toggleFollowingProgress}
                followingInProgress={this.props.followingInProgress}
            />

        </>
    }

}

