import {profileAPI } from '../api/api';
const ADD_POST = 'ADD-POST';
const SET_USER_PROFILE = 'SET_USER_PROFILE';
const SET_STATUS = 'SET_STATUS';

let initialState = {
  posts: [
      { id: 1, likesCount: 15, message: 'Hey, Im learning React JS' },
      { id: 2, likesCount: 20, message: 'No maney No funnies ' },
      { id: 3, likesCount: 33, message: 'Astala Vista Baby!' }
    ],
    profile: null,
    status:"",
   
};


const profileReducer = (state=initialState, action ) => { //action приходит через dispatch в MyPosts, в action лeжит
  //функция addPostActionCreator являющаяся аргументов в вызове функции диспач в MyPostContainer
  
    switch(action.type) {
      case ADD_POST: {
        return {
          ...state,
          posts:[...state.posts,  {id: 4, message: action.formData, likesCount: 0} ],
        };
      }
        case SET_USER_PROFILE: {
        return {
          ...state,
          profile:action.profile  
        };
      }
        case SET_STATUS:{
          return {
            ...state,
            status:action.status
          };  
        } 

      default:
        return state;
    }  

};

// 2 функции ниже возврашают объект action в аргумент в методе dispatch, который проверяет 
//на условия наличия опре- го  значения  свойства type
export const addPostActionCreator = (formData) => {
  return {
    type: ADD_POST,
    formData
  };
};
export const setUserProfile = (profile) => {
  return {
    type: SET_USER_PROFILE,
    profile: profile
  };
};
export const setStatus = (status) => {
  return {
    type: SET_STATUS,
    status
  };
};

 
export const setProfile = (userId) => async (dispatch) => {
    let data = await profileAPI.getProfile(userId);
    dispatch(setUserProfile(data));
  };

export const getStatus = (userId) => {
  return async (dispatch) =>{
    let response = await profileAPI.getStatus(userId);
      dispatch(setStatus(response.data));
  };
};

export const updateStatus = (status) => async (dispatch) =>{ 
    let response =  await profileAPI.updateStatus(status);
      if( response.data.resultCode === 0) {
      dispatch(setStatus(status));
    }
};
export default profileReducer;  

