import { usersAPI } from '../api/api';
import { updateObjectArray } from '../utils/object-helpers';
const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';
const SET_USERS = 'SET_USERS';
const SET_CURRENT_PAGE = 'SET_CURRENT_PAGE';
const SET_TOTAL_USERS = 'SET_TOTAL_USERS';
const TOGGLE_IS_FETCHING = 'TOGGLE_IS_FETCHING';
const TOGGLE_IS_FOLLOWING_PROGRESS= 'TOGGLE_IS_FOLLOWING_PROGRESS';


let initialState = {
    users: [],
    pageSize: 10,
    totalUsersCount:0,
    currentPage:1,
    isFetching: true,
    followingInProgress: []
};


const userReducer = (state=initialState, action ) => { 
    switch(action.type) {
        case FOLLOW: 
            return {
                ...state,
                users: updateObjectArray(state.users, action.userId, "id", {followed:true})
            //     users:state.users.map(user =>{
            //         if (user.id === action.userId){ 
            //             return {...user,  followed:true}; 
            //     }
            //     return user;
            // })
         };
        case UNFOLLOW:
            return {
                ...state,
                users: updateObjectArray(state.users, action.userId, "id", {followed:false})
            };
        
            case SET_USERS: {
            return{ ...state, users: action.users };
            //к имеющимуся массиву users, добавляем юзеров которые пришли в action
         }
             case SET_CURRENT_PAGE: {
                 return{ ...state, currentPage: action.currentPage };
            
             }
             case SET_TOTAL_USERS: {
                return{ ...state,  totalUsersCount: action.totalUsersCount };
           
            }
            case TOGGLE_IS_FETCHING: {
                return{ ...state,  isFetching: action.isFetching };
           
            }
            case TOGGLE_IS_FOLLOWING_PROGRESS: {
                return{ 
                    ...state,  
                    followingInProgress: action.followingInProgress ?
                    [...state.followingInProgress, action.userId]
                    : state.followingInProgress.filter(id =>id!=action.userId)
// //На страничке мы нажимаем кнопку follow/unfollow, срабатывает обработчик событий и вызывает функцию ToggleFollowingProgress, 
// передавая в качестве параметра isFetching true  и айди пользователя, кнопку которого нажали. 
// // В редьюсер отправляется соответствующий action и поскольку из компоненты было передано 
// true в качестве isFetching срабатывает первое условие в тернарном выражении,то есть айди 
// пользователя (переданное при вызове колбека) добавляется в массив followingInProgress
// // В компоненте на кнопке висит disabled который проверяет есть ли в массиве FollowingInProgress  
// айди пользователя и если есть то кнопка дизейблится.
// // Далее когда мы запостили\делитнули запрос и получили ответ, мы изменяем кнопку на странице на другое значение - 
// unfollow\follow и снова вызывается функция ToggleFollowingProgress с новыми аргументами - false и этим же айди пользователя
// // В редьюсере, поскольку пришел false срабатывает второе условие тернарного выражения и фильтруется массив  
// FollowingInProgress  - в нем остаются только те айди, которые НЕ совпадают с переданным при вызове колбека, 
// то есть эта фильтрация удаляет пришедший айди из массива
// // Поскольку теперь в массиве нет этого айди то и в дизейбле метод some\includes его не найдет и кнопка не будет дизейблится
// // Если в массиве FollowingInProgress  есть айди пользователя то кнопка дизейблится а если отфильтровать то не дизейбл
        
                };
            }

        default:
          return state;
      }  
  
  };
        


//userId переадет из Users callbackом
export const followSuccess = (userId) => ({type: FOLLOW, userId});
export const unfollowSuccess = (userId) => ({type:UNFOLLOW, userId});
export const setUsers = (users) => ({type:SET_USERS, users});
export const setCurrentPage = (currentPage) => ({type:SET_CURRENT_PAGE, currentPage});
export const setTotalUsersCount = (totalUsersCount) => ({type:SET_TOTAL_USERS, totalUsersCount});
export const toggleIsFetching = (isFetching) => ({type:TOGGLE_IS_FETCHING, isFetching});
export const toggleFollowingProgress = (followingInProgress, userId) => 
({type:TOGGLE_IS_FOLLOWING_PROGRESS, followingInProgress, userId});
   
export const getUsersThankCreator = (currentPage, pageSize) => async (dispatch)  => {
        dispatch(toggleIsFetching(true));
        dispatch(setCurrentPage(currentPage));
        let data = await usersAPI.getUsers(currentPage, pageSize);
        dispatch(toggleIsFetching(false));
        dispatch(setUsers(data.items));
        dispatch(setTotalUsersCount(data.totalCount));
}; 

const followUnfollowFlow = async (dispatch, userId, apiMethod, actionCreator) => {
    dispatch(toggleFollowingProgress(true, userId));
        let data = await apiMethod(userId);
            if (data.resultCode == 0) {
                dispatch(actionCreator(userId));
            }
            dispatch(toggleFollowingProgress(false, userId));
};

export const follow = (userId) => async(dispatch) => {
    followUnfollowFlow(dispatch, userId, usersAPI.followUser.bind(usersAPI),followSuccess);
};
export const unfollow = (userId) => async (dispatch) => {
    followUnfollowFlow(dispatch, userId, usersAPI.unfollowUser.bind(usersAPI), unfollowSuccess);
};
export default userReducer;

