
const SEND_MESSAGE = 'SEND-MESSAGE';

let initialState ={
  dialogs: [
      { id: 1, name: 'Timur' },
      { id: 2, name: 'Veronika' },
      { id: 3, name: 'Robert' },
      { id: 4, name: 'Dakota' },
      { id: 5, name: 'Vagiz' },
      { id: 6, name: 'Iskander' }
    ],
      messages:[
          { id: 1, message: 'My life for the Hord!' },
          { id: 2, message: 'Work work'},
          { id: 3, message: 'Unleash the beast!'},
          { id: 4, message: 'For the Horde!'},
          { id: 5, message: 'Lok Tar Ogar warrior!'},
        ]
        
};

const dialogsReducer = ( state=initialState, action,) => {

      switch(action.type) {
        case SEND_MESSAGE: 
         return {
          //копируем state и messages тк его изменяем, 
            ...state,
            messages: [...state.messages,  {id:4,message:action.newMessageBody}],// пушим новый объект в messages
          };
       
        default:
          return state;     
  }
  
};
// 2 функции ниже возврашают объект action в аргумент в методе dispatch, который проверяет 
// на условия наличия опре- го  значения  свойства type
export const sendMessageCreator = (newMessageBody) => {
  return {
    type: SEND_MESSAGE,
    newMessageBody
  };
};


export default dialogsReducer;