export const updateObjectArray = (items, itemId, objPropName, newObjProps) => {
    return items.map(i =>{
        if (i[objPropName] === itemId){ // если id объекта === id объекта клика (action)
            //то меняется свойство followed и кнопку в Users
            //копируем объект user(один user из массива users) и меняем значение свойства
            return {...i, ...newObjProps}; 
    }
    return i;
});
}