
import { React, useState } from "react";
import s from './Paginator.module.css';



let Paginator = ({ currentPage, onPageChanged, totalItemsCount, pageSize, portionSize = 10 }) => {
    // console.log(props)
    let pagesCount = Math.ceil(totalItemsCount / pageSize);
    //Math.ceil() округляет в большую сторону
    let pages = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i); //в массив добавляются цифпы- количество страниц

    }

    let portionCount = Math.ceil(pagesCount / portionSize);
    let [portionNumber, setPortionNumber] = useState(1);
    let leftPotionNumber = (portionNumber - 1) * portionSize + 1;
    let rightPotionNumber = portionNumber * portionSize;


    return <div className={s.paginator}>
        {portionNumber > 1 &&
            <button onClick={() => { setPortionNumber(portionNumber - 1) }}>Prev</button>}
        {pages.filter(p => p >= leftPotionNumber && p <= rightPotionNumber)
            .map(p => {
                return <span onClick={() => { onPageChanged(p) }}
                    className={currentPage === p ? s.selectedPage : ""}> - {p}</span>
            })}
        {portionCount > portionNumber &&
            <button onClick={() => { setPortionNumber(portionNumber + 1) }}>Next</button>}
    </div>



}


export default Paginator;

