import React from "react";
import { reduxForm, Field } from 'redux-form';
import { Input, createField } from '../common/FormsControls/FormsControls';
import { maxLengthCreator, required } from '../../utils/validators/validators';
import { connect } from "react-redux";
import { login } from '../../redux/auth-reducer';
import { Navigate } from "react-router-dom";
import s from '../common/FormsControls/FormsControls.module.css';



const maxLength30 = maxLengthCreator(30);

const LoginForm = ({ handleSubmit, error }) => {

    return (
        // чтобы не ставить для кадого инпута onChage из пользуем компоненты из библиотеки Field
        //в них уже заложены onChange и другие вещи

        // у любой формы в нативном JS есть событие onSubmit. Мы передаем данные submit на обработку
        //коллбэку handleSubmit который пришел из reduxForm.
        //handleSubmit - делает 1. e.preventDefault. 2 get all form data and put them to object.
        // 3.Внутри reduxForm вызывается onSubmit(login{formData}) и передает туда обьект данных с формы составленный 
        //handleSubmit
        <form onSubmit={handleSubmit}>

            {createField("Email", "email", [required, maxLength30], Input)}
            {/* <Field validate={[required, maxLength30]} placeholder={'Email'} name={'email'} component={Input} /> */}
            {createField("Password", 'password', [required, maxLength30], Input, "password")}
            {createField(null, 'rememberMe', [], Input, "checkbox")}remember Me

            {error && <div className={s.formSummaryError}>
                {error}
            </div>}
            <div>
                <button>Login</button>
            </div>
        </form>
    )
}

const LoginReduxForm = reduxForm({
    form: 'login' //как в connect создает невидимую контейнерную компоненту которая будет делать
    //диспатчи внутри себя. 
    // Как mapStateToProps, mapStateToProps нужно указать, что конктретно нужно их стейта
    //в нашем случае оуказывает  form: 'login' которая создалось в reduxForm
})(LoginForm)


const Login = (props) => {
    const onSubmit = (formData) => {
        props.login(formData.email, formData.password, formData.rememberMe)
    }
    //onSubmit={onSubmit} передаем в контейнерный LoginReduxForm колл бэк onSubmit, куда handleSubmit отправит formData
    //
    if (props.isAuth) {
        return <Navigate to={'/profile'} />
    }
    return <div>
        <h1>Login</h1>
        <LoginReduxForm onSubmit={onSubmit} />
    </div>
}

const mapStateToProps = (state) => ({
    isAuth: state.auth.isAuth
})

export default connect(mapStateToProps, { login })(Login);